import React, { memo } from "react";
import { Link } from "react-router-dom";
import dayjs from "dayjs";

import { Button, Dropdown, Menu, Modal, Table, TableProps } from "antd";

import { ExclamationCircleOutlined } from "@ant-design/icons";

import Pin from "../../components/pin";
import { useCollectProject, useDeleteProject } from "../../hooks/useProject";
import { useProjectModal, useProjectSeachParams } from "./util";
import { User } from "../../types/project";

interface ListProps extends TableProps<any>{
  users: User[],
}

const { confirm } = Modal;

function List({ users, ...props }: ListProps) {
  const [searchParams] = useProjectSeachParams()
  const queryKey = ['projectList',searchParams]
  const { mutate: collectMutate } = useCollectProject(queryKey)
  const { mutate: deleteMutate } = useDeleteProject(queryKey)

  const { startEdit } = useProjectModal()
  // useDeleteProject

  const projectEdit = (id:number) => startEdit(id)

  const projectDelete = (id:number) => {
    confirm({
      title: '您确定要删除当前选中内容?',
      icon: <ExclamationCircleOutlined />,
      content: '这是一条删除提示',
      onOk() {
        deleteMutate(id)
      },
    });
  }

  return (
    <Table
      rowKey="id"
      pagination={false}
      columns={[
        {
          title: <Pin checked={true} disabled/> ,
          render: (value,project) => {
            return (
              <Pin
                checked={project.pin}
                onCheckedChange={ pin => {
                  collectMutate({id: project.id,pin})
                }
                }
              />
            )
          }
        },
        {
          title: '名称',
          // sorter:(a,b) => a.name.localeCompare(b.name),
          render: (_,project) => <Link to={String(project.id)}>{ project.name }</Link>
        },
        {
          title: "部门",
          dataIndex: "organization",
        },
        {
          title: "负责人",
          render: (_,project) => (
            <div> { users.find(item => item.id === project.personId)?.name || '未知' }</div>
          )
        },
        {
          title: '创建时间',
          dataIndex: 'created',
          render: (time) => (
            <span> { time && dayjs(time).format("YYYY-MM-DD")  } </span>
          )
        },
        {
          title: '编辑',
          render: ({ id }) => (
            <Dropdown overlay={
              <Menu>
                <Menu.Item key={'edit'} onClick={() => projectEdit(id)}>
                  编辑
                </Menu.Item>
                <Menu.Item key={'deleted'} onClick={() => projectDelete(id)}>
                  删除
                </Menu.Item>
              </Menu>
            }>
              <Button style={{padding: 0}} type={'link'}>...</Button>
            </Dropdown>
          )
        }
      ]}
      { ...props }
    />
  )
}

export default memo(List)


