import { useState, useEffect } from 'react'
// 节流hooks
export default function useDebounce<T>(values: T ,delay: number = 2000) {
  const [ debounceValue, setDebounceValue ] = useState(values)
  useEffect(() => {
    // 每次在values变化的时候,设置一个定时器
    const timeout = setTimeout(() => setDebounceValue(values),delay)
    // 每次在上一个useEffect处理完以后在运行
    return () => clearTimeout(timeout)
  },[values,delay])
  return debounceValue
}

