import { useGetProjectList } from "../../hooks/useProject";
import styled from "@emotion/styled";

import { Popover, Typography, List, Divider, Button } from "antd";
import { useProjectModal } from "../../screens/project-list/util";


const { Text } = Typography
export default function ProjectPopover() {
  const { projectModalOpen } = useProjectModal()

  const { data: projects, refetch } = useGetProjectList()
  const pinnedProjects = projects?.filter(item => item.pin)
  const content = (
    <ContentContainer>
      <Text type={'secondary'}>收藏项目</Text>
      <List>
        {
          pinnedProjects?.map(item => (
            <List.Item.Meta key={item.id} title={item.name}/>
          ))
        }
      </List>
      <Divider/>
      <Button
        style={{padding: 0}}
        type={'link'}
        onClick={projectModalOpen}>
        创建项目
      </Button>
    </ContentContainer>
  )

  return (
    <Popover onVisibleChange={() => refetch()} placement={'bottom'} content={content}>
      <span>项目</span>
    </Popover>
  )
}

const ContentContainer = styled.div`
  min-width: 30rem;
`
