import { URLSearchParamsInit, useSearchParams } from "react-router-dom";
import { useMemo } from "react";
import { clearObject } from "../utils";

// 返回页面url中,指定键的参数值
export default function useUrlQueryParam<K extends string>(keys: K[]) {
  const [ searchParams, setSearchParam ] = useSearchParams()
  return [
    useMemo(() => {
      return keys.reduce((acc,curr) => {
        return { ...acc, [curr]: searchParams.get(curr) || '' }
      },{} as { [curr in K]: string } ) // as关键字 用于自定义值类型
      // eslint-disable-next-line react-hooks/exhaustive-deps
    },[searchParams]),
    (parmas: Partial<{[curr in K]: unknown}>) => {
      const obj = clearObject({ ...Object.fromEntries(searchParams), ...parmas  })  as URLSearchParamsInit
      return setSearchParam(obj)
    }
  ] as const
}
