import { createContext, ReactNode, useContext } from "react";

import { FullPageloading, FullPageErrorFallback } from '../components/lib'
import * as auth from 'auth-provider'
import { localStorageKey } from "auth-provider";
import { http } from "../servers/http";
import useMount from "../hooks/useMount";
import useAsync from "../hooks/useAsync";
import { useQueryClient } from "react-query";
import { User } from "../types/project";

interface AuthForm {
  username: string,
  password: string
}

const bootstrapUser = async () => {
  let user = null
  const token = window.localStorage.getItem(localStorageKey)
  if (token) {
    const result = await http('me',{ token })
    user = result.user
    return user
  }
}

export const AuthContext = createContext<{
  user: User | null,
  login: (form: AuthForm) => Promise<void>,
  register: (form: AuthForm) => Promise<void>,
  logout: () => void
} | undefined >(undefined)
AuthContext.displayName = 'AuthContext'

export const AuthProvider = ({children}: { children: ReactNode }) => {
  const { run, data: user, isLoading, isIdle, isError, error, setData: setUser } = useAsync<User | null>()
  const login = (form: AuthForm) => auth.login(form).then(user => setUser(user))
  const register = (form: AuthForm) => auth.register(form).then(user => setUser(user))

  const queryClient = useQueryClient()

  const logout = () => auth.logout().then( () => {
    setUser(null)
    queryClient.clear() // 清除所有数据
  })

  // token 免密登录
  useMount(() => {
    run(bootstrapUser())
  })
  // 开始加载或者加载未结束，显示loading页面
  if (isIdle || isLoading) {
    return <FullPageloading/>
  }

  // 如果bootstrapUser发生错误,显示error页
  if (isError) {
    return <FullPageErrorFallback error={error}/>
  }

  return (
    <AuthContext.Provider
      value={{
        user,
        login,
        register,
        logout
      }}>
      { children }
    </AuthContext.Provider>
  )
}

export const useAuth = () => {
  const context = useContext(AuthContext)
  if(!context) {
    throw new Error('useAuth必须在AuthProvider中使用')
  }
  return context
}
