import { QueryKey, useMutation, useQuery } from "react-query";
import { Epic } from "../types/epic"
import useHttp from "./useHttp";

import { useAddConfig, useDeleteConfig } from "./useConfig";

// 获取看板列表
export function useGetEpicList(param?: Partial<Epic>) {
  const client = useHttp()
  // param变化时也会自动触发
  return useQuery<Epic[],Error>(
    ['epics',param], () => client('epics',{ data: param }))
}

// 添加看板
export function useAddEdic(queryKey: QueryKey) {
  const client = useHttp()
  return useMutation(
    (params:Partial<Epic>) => client(`epics`,{
      method: 'POST',
      data: params
    }),
    useAddConfig(queryKey)
  )
}

// 删除看板
export function useDeleteEdic(queryKey:QueryKey) {
  const client = useHttp()
  return useMutation(
    (id: number) => client(`epics/${id}`,{
      method: 'DELETE'
    }),
    useDeleteConfig(queryKey),
  )
}
