import { useState } from "react";

import {
  Button, Card, Divider, Typography
} from "antd";

import Login from "./login";
import Register from "./register";

import useDocumentTitle from "../hooks/useDocumentTitle";


import styled from '@emotion/styled'
import logo from '../assets/logo.svg'
import left from '../assets/left.svg'
import right from '../assets/right.svg'

export function UnauthenticatedApp() {
  const [ authState, setAuthState ] = useState(false)
  const [ error, setError ] = useState<null | Error>(null)

  useDocumentTitle('请登录注册以继续')

  return (
    <Container>

      <Header/>
      <Background/>
      <ShadowCard>
        <Title> { authState ? '请登录' : '请注册' } </Title>
        { error ? <Typography.Text type={'danger'}>{ error.message }</Typography.Text> : null }
        { authState ? <Login onError={setError}/> : <Register onError={setError}/> }
        <Divider/>
        <Button type={'link'}   onClick={ ev => setAuthState(!authState)}>
          { authState ? '没有账号? 快去注册一个吧' : '已经有账号了? 直接登录' }
        </Button>
      </ShadowCard>
    </Container>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
`
const ShadowCard = styled(Card)`
  box-sizing: border-box;
  width: 40rem;
  min-height: 56rem;
  padding: 3.2rem 4rem;
  text-align: center;
  border-radius: 0.3rem;
  box-shadow: rgba(0,0,0,0,1) 0 0 10px;
`

const Header = styled.div`
  background: url(${logo}) no-repeat center;
  padding: 5rem 0;
  background-size: 8rem;
  width: 100%;
`
const Background = styled.div`
  position: absolute;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed; // 跟随页面滚动
  background-position: left bottom, right bottom ;
  background-size: calc(((100vw - 40rem ) / 2) - 3.2rem ),calc(((100vw - 40rem ) / 2) - 3.2rem ),cover;
  background-image: url(${left}), url(${right});
`

const Title = styled.h2`
  margin-bottom: 2.4rem;
  color: rgb(94,108,132);
`

export const LongButton = styled(Button)`
  width: 100%;
`
