import React, { useEffect } from "react";
import styled from "@emotion/styled";
import { Drawer, Button, Form, Input, Spin, Typography } from "antd";

import UserSelect from '../../components/user-select'
import { useProjectModal, useProjectSeachParams } from "./util";
import { useAddProject, useEditProject } from "../../hooks/useProject";

export default function ProjectModal() {
  const [form] = Form.useForm()

  const { projectModalVisible, projectModalClose, editProject, isLoading } = useProjectModal()

  const title = editProject ? '编辑项目' : '创建项目'

  const useMutateProject = editProject ? useEditProject : useAddProject

  const [searchParams] = useProjectSeachParams()
  const queryKey = ['projectList',searchParams]
  const { mutateAsync , isLoading: mutateLoading, error } = useMutateProject(queryKey)

  const onFinish = (values:any) => {
    mutateAsync({ ...editProject,...values }).then(() => {
      form.resetFields()
      projectModalClose()
    })
  }

  useEffect(() => {
    if (editProject) {
      form.setFieldsValue(editProject)
    }
    return () => {
      form.resetFields()
    }
  },[editProject, form])

  return (
    <Drawer
      forceRender={true}
      width={'100%'}
      visible={projectModalVisible}
      onClose={projectModalClose}
    >
      <Container>
        { isLoading
          ? <Spin size={'large'}/>
          : (
            <>
              <h1>{title}</h1>
              {/*  @ts-ignore */}
              { error ? <Typography.Text type={'danger'}>{error?.message}</Typography.Text> : null }
              <Form
                form={form}
                layout={'vertical'}
                style={{ width: '40rem' }}
                onFinish={onFinish}
              >
                <Form.Item
                  label={'名称'}
                  name={'name'}
                  rules={[{ required: true}]}
                >
                  <Input placeholder={'请输入项目名称'}/>
                </Form.Item>

                <Form.Item
                  label={'部门'}
                  name={'organization'}
                  rules={[{ required: true}]}
                >
                  <Input placeholder={'请输入部门名'}/>
                </Form.Item>

                <Form.Item
                  label={'负责人'}
                  name={'personId'}
                >
                  <UserSelect defaultOptionName={'负责人'}/>
                </Form.Item>
                <Form.Item style={{ textAlign: "right" }}>
                  <Button loading={mutateLoading}  type={'primary'} htmlType={"submit"}>提交</Button>
                </Form.Item>
              </Form>
            </>
          )
        }
      </Container>

    </Drawer>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  height: 80vh;
  justify-content: center;
  align-items: center;
`
