import { useCallback, useMemo } from "react";
import useUrlQueryParam from "../../hooks/useUrlQueryParam";
import { useGetProjectItem } from "../../hooks/useProject";

export const useProjectSeachParams = () => {
  const [ param, setParam ] = useUrlQueryParam(['name','personId'])
  return [
    useMemo(() => ({ ...param, personId: Number(param.personId) || undefined }),[param]),
    setParam
  ] as const
}

export const useProjectModal = () => {
  const [ { projectModalVisible }, setProjectModalVisible ] = useUrlQueryParam(['projectModalVisible'])

  const [ { editID }, setEditID ] = useUrlQueryParam(['editID'])
  let { data: editProject, isLoading } = useGetProjectItem(Number(editID))
  const projectModalOpen = () =>  setProjectModalVisible({ projectModalVisible: true })

  const projectModalClose = useCallback(() => {
    setProjectModalVisible({ projectModalVisible: false })
    editID && setEditID({ editID: undefined })
  },[editID, setEditID, setProjectModalVisible])

  const startEdit = useCallback((id:number) =>{
    setEditID({ editID: id})
  },[setEditID])

  return {
    projectModalVisible: projectModalVisible === 'true' || Boolean(editID) , // 这里是因为通过url获取的是字符串
    editProject,
    isLoading,
    projectModalOpen,
    projectModalClose,
    startEdit
  }
}
