import styled from "@emotion/styled";
import { DragDropContext, DropResult } from "react-beautiful-dnd";
import { Spin } from 'antd'

import KanbanColumn from "./kanban-column";
import SeachPanel from "../kanban/search-panel";
import CreateKanban from "./create-kanban";
import { ScreenContainer } from "../../components/lib";
import { Drag, Drop, DropChild } from "../../components/drap-and-drop";

import useDocumentTitle from "../../hooks/useDocumentTitle";
import { useGetKanbans, useReorderKanban } from "../../hooks/useKanbans";
import {
  useKanbanSearchParams,
  useKanbansQueryKey,
  useProjectInUrl,
  useTaskSearchParams,
  useTasksQueryKey
} from "./util";
import { useGetTaskList, useReorderTask } from "../../hooks/useTasks";
import TaskModal from "./task-modal";
import { useCallback } from "react";

export const DROP_TYPE = {
  'kanban': 'COLUMN',
  'task': 'ROW'
}

export default function KanbanScreen() {

  useDocumentTitle('看板列表')
  const { data: currentProject } = useProjectInUrl()
  const { data: kanbanList, isLoading: kanbanIsLoading } = useGetKanbans(useKanbanSearchParams())
  const { data: taskList ,isLoading: taskIsLoading  } = useGetTaskList(useTaskSearchParams())

  const isLoading = taskIsLoading || kanbanIsLoading
  const onDragEnd = useDragEnd()
  return (
    <DragDropContext onDragEnd={onDragEnd}
    >
      <ScreenContainer>
        <h1>{currentProject?.name}看板 </h1>
        <SeachPanel/>
        {
          isLoading
            ? <Spin size={"large"}/>
            : (
              <KanbanContainer>
                <Drop type={DROP_TYPE.kanban} direction={"horizontal"} droppableId={"kanban"}>
                  <DropChild style={{display:"flex"}}>
                    {
                      kanbanList?.map((item,idx) => (
                        <Drag key={item.id} draggableId={"kanban" + item.id} index={idx}>
                          <KanbanColumn
                            kanban={item}
                            taskList={taskList || []}
                          />
                        </Drag>
                      ))
                    }
                  </DropChild>
                </Drop>
                <CreateKanban/>
              </KanbanContainer>
            )
        }
        <TaskModal/>
      </ScreenContainer>
    </DragDropContext>
  )
}

export const useDragEnd = () => {
  const { data: kanbanList } = useGetKanbans(useKanbanSearchParams())
  const { data: taskList = []  } = useGetTaskList(useTaskSearchParams())
  const { mutate: reorderKanban } = useReorderKanban(useKanbansQueryKey())
  const { mutate: reorderTask } = useReorderTask(useTasksQueryKey())

  return useCallback(({source,destination,type}: DropResult) => {
    if(!destination) {
      return
    }
    // 看板排序
    if (type === DROP_TYPE.kanban) {
      // 根据索引 获取当前需要移动的元素所在看板列表中的id
      const fromId = kanbanList?.[source.index].id
      // 获取目标元素id
      const referenceId = kanbanList?.[destination.index].id
      if(!fromId || !referenceId || fromId === referenceId) {
        return;
      }
      const type = destination.index > source.index ? 'after' : 'before'
      reorderKanban({fromId, referenceId, type })
    }

    // task排序
    if(type === DROP_TYPE.task) {
      const fromKanbanId = Number(source.droppableId)
      const toKanbanId = Number(destination.droppableId)
      // 不允许 非跨跃看板排序
      if(fromKanbanId === toKanbanId) {
        return;
      }
      // 获取当前task的id
      const fromTask = taskList
        .filter(item => Number(item.kanbanId) === fromKanbanId)[source.index]
      // 目标元素id
      const toTask = taskList
        .filter(item => Number(item.kanbanId) === toKanbanId)[destination.index]
      if (fromTask?.id === toTask?.id) {
        return;
      }
      reorderTask({
        fromId: fromTask?.id,
        referenceId: toTask?.id,
        fromKanbanId,
        toKanbanId,
        type: fromKanbanId === toKanbanId && destination.index > source.index ? 'after' : 'before'
      })
    }
  },[kanbanList, reorderKanban, reorderTask, taskList])
}

export const KanbanContainer = styled.div`
  display: flex;
  flex: 1;
  overflow-x: scroll;
`
