import useHttp from "./useHttp";
import { QueryKey, useMutation, useQuery } from "react-query";
import { Kanban } from "../types/kanban";
import { useAddConfig, useDeleteConfig, useReorderKanbanConfig } from "./useConfig";

// 获取看板列表
export function useGetKanbans(param?: Partial<Kanban>) {
  const client = useHttp()
  // param变化时也会自动触发
  return useQuery<Kanban[],Error>(
    ['kanbans',param], () => client('kanbans',{ data: param }))
}

// 添加看板
export function useAddKanban(queryKey: QueryKey) {
  const client = useHttp()
  return useMutation(
    (params:Partial<Kanban>) => client(`kanbans`,{
      method: 'POST',
      data: params
    }),
    useAddConfig(queryKey)
  )
}

// 删除看板
export function useDeleteKanban(queryKey:QueryKey) {
  const client = useHttp()
  return useMutation(
    (id: number) => client(`kanbans/${id}`,{
      method: 'DELETE'
    }),
    useDeleteConfig(queryKey),
  )
}


export interface SortProps {
  // 需要排序的 item
  fromId: number,
  // 目标 item
  referenceId: number,
  // 放在目标item 前面还是后面
  type: 'before' | 'after',
  fromKanbanId?: number,
  toKanbanId?: number
}

// 移动看板
export function useReorderKanban(queryKey:QueryKey) {
  const client = useHttp()
  return useMutation(
    (params: SortProps) => client(`kanbans/reorder`,{
      method: 'POST',
      data: params
    }),
    useReorderKanbanConfig(queryKey)
  )
}



