
export default function Mark({ text, keyword }: { text: string, keyword: string | undefined }) {
  if (!keyword) {
    return (<>{text}</>)
  }
  const arr = text.split(keyword) // 得到当前没有和keyword匹配上的
  return (
    <>
      {
        arr.map((str,idx) => (
          <span key={idx}>
            { str }
            {
              idx === arr.length - 1
                ? null
                : <span style={{ color: '#257AFD' }}>{keyword}</span>
            }
          </span>
        ))
      }
    </>
  )
}
