import { useCallback, useReducer, useState } from "react";
import useMountedRef from "./useMountedRef";

interface State<D> {
  error: Error | null
  data: D | null
  stat: 'idle' | 'loading' | 'error' | 'success' // 加载过程
}

const defaultInitialState: State<null> = {
  error: null,
  data: null,
  stat: 'idle'
}

const defaultConfig = {
  throwOnError: false
}

const useSafeDispatch = <T>(dispatch: (...args:T[]) => void) => {
  const MountedRef = useMountedRef()
  return useCallback((...args:T[]) => ( MountedRef.current ? dispatch(...args) : void 0),[MountedRef, dispatch])
}

// 处理异步请求,返回加载状态、错误信息、重新请求的方法等信息
export default function useAsync<D>(initialState?: State<D>,initialConfig: {throwOnError: boolean} = defaultConfig) {

  const [ state, dispatch ] = useReducer((state: State<D>,action: Partial<State<D>>) => ({ ...state, ...action }),{
    ...defaultInitialState, ...initialState
  })

  const [ retry, setRetry ] = useState(() => () => {
    console.log('1');
  })



  const safeDispatchRef = useSafeDispatch(dispatch)

  const setData = useCallback((data:D) => safeDispatchRef({
    error: null,
    data,
    stat: 'success'
  }),[safeDispatchRef])

  const setError = useCallback((error: Error) => safeDispatchRef({
    error,
    data: null,
    stat: 'error'
  }),[safeDispatchRef])

  // run用来触发异步请求    useCallback只有在依赖发生变化时,才会重新定义
  const run = useCallback((promise: Promise<D>, runConfig?: { retry: () => Promise<D> }) => {
    if (!promise || !promise.then) {
      throw new Error('请传入Promise类型数据')
    }

    setRetry(() => () => {
      if (runConfig?.retry) {
        run(runConfig.retry(), runConfig)
      }
    })

    // setState({ ...state, stat: 'loading' })
    safeDispatchRef({ stat: 'loading' })
    return promise.then(data => {
      setData(data)
      return data
    }).catch(error => {
      // catch会消化异常,如果不主动抛出,外面是接收不到的
      setError(error)
      if (initialConfig.throwOnError) {
        return Promise.reject(error)
      }
      return error
    })
  },[initialConfig.throwOnError, safeDispatchRef, setData, setError])

  return {
    isIdle: state.stat === 'idle',
    isLoading: state.stat === 'loading',
    isError: state.stat === 'error',
    isSuccess: state.stat === 'success',
    run,
    setData,
    setError,
    // retry 被调用时,重新执行 run,让state刷新一遍
    retry,
    ...state
  }
}
