import { useLocation } from "react-router-dom";
import { useQuery } from "react-query";
import { useGetProjectItem } from "../../hooks/useProject";
import useHttp from "../../hooks/useHttp";
import { TaskType } from "../../types/task-type";
import useUrlQueryParam from "../../hooks/useUrlQueryParam";
import { useCallback, useMemo } from "react";
import { useGetTaskItem } from "../../hooks/useTasks";

// 获取当前地址栏中的id
export function useProjectIdInUrl() {
  const { pathname } = useLocation()
  const id = pathname.match(/project\/(\d+)/)?.[1]
  return Number(id)
}

export function useTaskTypes() {
  const client = useHttp()
  return useQuery<TaskType[]>('tasktypes',() => {
    return client('tasktypes')
  })
}

// 根据id获取当前item数据
export const useProjectInUrl = () => useGetProjectItem(useProjectIdInUrl())
//
export const useKanbanSearchParams = () => ({ projectId: useProjectIdInUrl()})

export const useKanbansQueryKey = () => ['kanbans', useKanbanSearchParams()]


export const useTaskSearchParams = () => {
  const [ param ] = useUrlQueryParam([
    'name',
    'typeId',
    'processorId',
    'tagId',
  ])
  const projectId = useProjectIdInUrl()
  return useMemo(() => {
    return {
      projectId,
      name: param.name || undefined,
      typeId: Number(param.typeId) || undefined,
      processorId: Number(param.processorId) || undefined,
      tagId: Number(param.tagId) || undefined,
    }
  },[projectId, param.name, param.typeId, param.processorId, param.tagId])
}

export const useTasksQueryKey = () => ['tasks', useTaskSearchParams()]

// task模态框状态控制
export const useTasksModal = () => {
  const [{ editingTaskId }, setEditingTaskId ] = useUrlQueryParam(['editingTaskId'])

  const { data: editTaskList, isLoading } = useGetTaskItem(Number(editingTaskId))

  // 打开模态框
  const startEdit = useCallback((id: number) => {
    setEditingTaskId({ editingTaskId: id })
  },[setEditingTaskId])

  // 关闭模态框
  const close = useCallback(() => {
    setEditingTaskId({ editingTaskId: null })
  },[setEditingTaskId])

  return {
    editTaskModalVisible: Boolean(editingTaskId),
    editingTaskId,
    editTaskList,
    isLoading,
    startEdit,
    close
  }
}
