import { QueryKey, useMutation, useQuery } from "react-query";
import { Task } from "../types/task";
import { useAddConfig, useDeleteConfig, useEditConfig, useReorderTaskConfig } from "./useConfig";
import useHttp from "./useHttp";
import { SortProps } from "./useKanbans";

// 获取任务列表
export function useGetTaskList(param?: Partial<Task>) {
  const client = useHttp()
  // param变化时也会自动触发
  return useQuery<Task[],Error>(
    ['tasks',param], () => client('tasks',{ data: param }))
}

export function useGetTaskItem(id?: number) {
  const client = useHttp()
  return useQuery<Partial<Task>, Error>(
    ['tasks', { id }], () => client(`tasks/${id}`),{
      enabled: Boolean(id) // 当id为空时不执行请求
    })
}

// 新增任务
export function useAddTask(queryKey: QueryKey) {
  const client = useHttp()
  return useMutation(
    (params:Partial<Task>) => client(`tasks`,{
      method: 'POST',
      data: params
    }),
    useAddConfig(queryKey)
  )
}

// 编辑任务
export function useEditTask(queryKey: QueryKey) {
  const client = useHttp()
  return useMutation(
    (params:Partial<Task>) => client(`tasks/${params.id}`,{
      method: 'PATCH',
      data: params
    }),
    useEditConfig(queryKey)
  )
}

// 删除任务
export function useDeleteTask(queryKey:QueryKey) {
  const client = useHttp()
  return useMutation(
    (id: number) => client(`tasks/${id}`,{
      method: 'DELETE'
    }),
    useDeleteConfig(queryKey),
  )
}

// 移动任务
export function useReorderTask(queryKey:QueryKey) {
  const client = useHttp()
  return useMutation(
    (params: SortProps) => client(`tasks/reorder`,{
      method: 'POST',
      data: params
    }),
    useReorderTaskConfig(queryKey)
  )
}
