import { QueryKey, useMutation, useQuery } from "react-query";
import useHttp from "./useHttp";
import { useCollectConfig, useAddConfig, useEditConfig,  useDeleteConfig } from "./useConfig";
import { Project } from "../types/project";

// 获取列表
export function useGetProjectList(param?: Partial<Project>) {
  const client = useHttp()
  // param变化时也会自动触发
  return useQuery<Project[],Error>(
    ['projectList',param], () => client('projects',{ data: param }))
}

// 根据 id获取对应数据
export function useGetProjectItem(id?: number) {
  const client = useHttp()
  return useQuery<Partial<Project>, Error>(
    ['project', { id }], () => client(`projects/${id}`),{
      enabled: Boolean(id) // 当id为空时不执行请求
      })
}

// 收藏列表
export function useCollectProject(queryKey:QueryKey) {
  const client = useHttp()
  return useMutation(
    (params:Partial<Project>) => client(`projects/${params.id}`,{
      method: 'PATCH',
      data: params
    }),
    useCollectConfig(queryKey)
  )
}

// 新增列表
export function useAddProject(queryKey: QueryKey) {
  const client = useHttp()
  return useMutation(
    (params:Partial<Project>) => client(`projects`,{
      method: 'POST',
      data: params
    }),
    useAddConfig(queryKey)
  )
}

// 编辑列表
export function useEditProject(queryKey: QueryKey) {
  const client = useHttp()
  return useMutation(
    (params:Partial<Project>) => client(`projects/${params.id}`,{
      method: 'PATCH',
      data: params
    }),
    useEditConfig(queryKey)
  )
}

// 删除
export function useDeleteProject(queryKey:QueryKey) {
  const client = useHttp()
  return useMutation(
    (id: number) => client(`projects/${id}`,{
      method: 'DELETE'
    }),
    useDeleteConfig(queryKey),
  )
}


