import { Link } from "react-router-dom";
import dayjs from "dayjs";
import { Button, List, Modal } from "antd";

import { ScreenContainer, Row } from "../../components/lib";

import { useProjectInUrl } from "../kanban/util";
import useDocumentTitle from "../../hooks/useDocumentTitle";
import { useGetEpicList, useDeleteEdic } from "../../hooks/useEpic";
import { useEpicQueryKey, useEpicSearchParams } from "./util";

import { useGetTaskList } from "../../hooks/useTasks";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import React, { useState } from "react";
import CreateEpic from "./create-epic";


export default function EpicScreen() {
  useDocumentTitle('任务组')
  const [ visible, setVisible ] = useState(false)

  const { data: currentProject } = useProjectInUrl()
  const { data: epicList } = useGetEpicList(useEpicSearchParams())
  const { data: taskList = [] } = useGetTaskList(useEpicSearchParams())
  const { mutate: deleteEdic } = useDeleteEdic(useEpicQueryKey())

  const onDelete = (id: number) => {
    Modal.confirm({
      title: '您确定要删除任务吗?',
      icon: <ExclamationCircleOutlined />,
      content: '这是一条删除提示',
      onOk() {
        deleteEdic(id)
      }
    })
  }

  const onClose = () => setVisible(false)

  return (
    <ScreenContainer>
      <Row between={true}>
        <h1>{currentProject?.name}任务组 </h1>
        <Button type={'link'} onClick={() => setVisible(true) }>创建任务组</Button>
      </Row>

      <List
        style={{ overflow: 'scroll' }}
        dataSource={epicList}
        itemLayout={'vertical'}
        renderItem={epic => (
          <List.Item>
            <List.Item.Meta title={
              <Row between={true}>
                <span>{ epic.name }</span>
                <Button type={'link'} onClick={() => onDelete(epic.id)}>删除</Button>
              </Row>
            }
            description={
              <div>
                <div>开始时间: { dayjs(epic.start).format('YYYY-MM-DD') }</div>
                <div>结束时间: { dayjs(epic.end).format('YYYY-MM-DD') }</div>
              </div>
            }
            />
            <div>
              {
                taskList
                  .filter(item => item.epicId === epic.id)
                  .map(task => (
                    <Link
                      style={{ display: "block" }}
                      key={task.id}
                      to={`/project/${currentProject?.id}/kanban?editingTaskId=${task.id}`}
                    >{ task.name }</Link>
                  ))
              }
            </div>
          </List.Item>
        )}
      />
      <CreateEpic visible={visible} onClose={onClose}/>
    </ScreenContainer>
  )
}
