import React,{ memo, } from "react";

import { Button, Typography } from "antd";

import SeachPanel from "./search-panel";
import { Row, ScreenContainer } from "../../components/lib";
import List from "./list";

import useDebounce from "../../hooks/useDebounce";
import { useGetProjectList } from "../../hooks/useProject";
import useUser from "../../hooks/useUser";
import useDocumentTitle from "../../hooks/useDocumentTitle";
import { useProjectModal, useProjectSeachParams } from "./util";

function ProjectList() {
  const [ param, setParam ] = useProjectSeachParams()
  const debounceParam = useDebounce(param,500)
  const { data: list, isLoading, error } = useGetProjectList(debounceParam)
  const { data: users } = useUser()

  const { projectModalOpen } = useProjectModal()

  useDocumentTitle('项目列表',false)

  return (
    // Container
    <ScreenContainer>
      <Row between={true}>
        <h1>项目列表</h1>
        <Button
          style={{padding: 0}}
          type={'link'}
          onClick={projectModalOpen}>
          创建项目
        </Button>
      </Row>

      <SeachPanel
        param={param}
        users={users || []}
        setParam={setParam}
      />
      { error ? <Typography.Text type={'danger'}>{error.message}</Typography.Text> : null   }
      <List
        users={users || []}
        dataSource={list || []}
        loading={isLoading}
      />
    </ScreenContainer>
  )
}
export default memo(ProjectList)

