import styled from "@emotion/styled";

import {
  Button, Drawer, DrawerProps,
  Form, Input, Typography
} from "antd";
import { useAddEdic } from "../../hooks/useEpic";
import { useEpicQueryKey } from "./util";
import { useProjectIdInUrl } from "../kanban/util";
import { useEffect } from "react";


// Pick 从定义好的类型属性中 挑选 出指定的一组属性,返回一个新的类型定义

export default function CreateEpic(props: Pick<DrawerProps, 'visible'> & { onClose: () => void } ) {
  const { mutate: addEdic, isLoading: mutateLoading, error } = useAddEdic(useEpicQueryKey())
  const projectId = useProjectIdInUrl()
  const [form] = Form.useForm()

  const onFinish = async (values:any) => {
    await addEdic({...values, projectId})
    props.onClose()
  }

  useEffect(() => {
    form.resetFields()
  },[form,props.visible])

  return (
    <Drawer
      width={'100%'}
      forceRender={true}
      destroyOnClose={true}
      visible={props.visible}
      onClose={props.onClose}
    >
      <Container>
        <>
          <h1>创建任务组</h1>
          {/*  @ts-ignore */}
          { error ? <Typography.Text type={'danger'}>{error?.message}</Typography.Text> : null }
          <Form
            form={form}
            layout={'vertical'}
            style={{ width: '40rem' }}
            onFinish={onFinish}
          >
            <Form.Item
              label={'名称'}
              name={'name'}
              rules={[{ required: true, message: '请输入任务组名'}]}
            >
              <Input placeholder={'请输入任务组名称'}/>
            </Form.Item>
            <Form.Item style={{ textAlign: "right" }}>
              <Button loading={mutateLoading}  type={'primary'} htmlType={"submit"}>提交</Button>
            </Form.Item>
          </Form>
        </>
      </Container>
    </Drawer>
  )
}

const Container = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 80vh;
`
