import React from "react";
import styled from "@emotion/styled";

import {
  Card, Dropdown, Button,
  Menu, Modal
} from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";

import { Kanban } from "../../types/kanban";
import { useKanbansQueryKey, useTaskSearchParams, useTasksModal, useTaskTypes } from "./util";
import taskIcon from 'assets/task.svg'
import bugIcon from 'assets/bug.svg'
import { Task } from "../../types/task";
import CreateTask from "./create-task";
import Mark from "../../components/mark";
import { useDeleteKanban } from "../../hooks/useKanbans";
import { Row } from "../../components/lib";
import { Drag, Drop, DropChild } from "../../components/drap-and-drop";
import { DROP_TYPE } from './index'

// task图标组价
const TaskTypeIcon = ({id}: {id: number }) => {
  const { data: taskTypes } = useTaskTypes()
  const name = taskTypes?.find(item => item.id === id)?.name
  if(!name) return null
  return <img src={ name === 'task' ? taskIcon : bugIcon }  alt='!'/>
}

// task卡片组件
const TaskCard = ({task}: { task: Task }) => {
  const { startEdit } = useTasksModal()
  const { name: keyword } = useTaskSearchParams()
  return (
    <Card
      style={{marginBottom: '.5rem', cursor: 'pointer'}}
      onClick={() => startEdit(task.id)}
    >
      <Mark text={task.name} keyword={keyword}/>
      <TaskTypeIcon id={task.typeId}/>
    </Card>
  )
}

const More = ({ kanban }: { kanban: Kanban } ) => {

  const { mutateAsync } = useDeleteKanban(useKanbansQueryKey())

  const startEdit = () => {
    Modal.confirm({
      title: '您确定要删除看板吗?',
      icon: <ExclamationCircleOutlined />,
      content: '这是一条删除提示',
      async onOk() {
        await mutateAsync(kanban.id)
      }
    })
  }

  const overlay = (
    <Menu>
      <Menu.Item key={'delete'}>
        <Button type={'link'} onClick={startEdit}>删除</Button>
      </Menu.Item>
    </Menu>
  )
  return (
    <Dropdown overlay={overlay}>
      <Button type={'link'}>...</Button>
    </Dropdown>
  )
}

export default React.forwardRef<HTMLDivElement,{ kanban: Kanban, taskList: Task[]}>(function KanbanColumn({ kanban, taskList,...props },ref) {
  const tasks = taskList?.filter(task => task.kanbanId === kanban.id)
  return (
    <Container { ...props } ref={ref}>
      <Row between={true}>
        <h3>{kanban.name}</h3>
        <More kanban={kanban} key={kanban.id}/>
      </Row>

      <TasksContainer>
        <Drop type={DROP_TYPE.task} direction={'vertical'} droppableId={ String(kanban.id)}>
          <DropChild style={{ minHeight: 5 }}>
            {
              tasks?.map((item,index) => (
                <Drag key={item.id} index={index} draggableId={'task' + item.id}>
                  <div>
                    <TaskCard task={item} />
                  </div>
                </Drag>
              ))
            }
          </DropChild>
        </Drop>

        <CreateTask kanbanId={kanban.id}/>
      </TasksContainer>
    </Container>
  )
})

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  padding: .7rem .7rem 1rem;
  margin-right: 1.5rem;
  min-width: 27rem;
  border-radius: 6px;
  background-color: rgb(244,245,247);
`

const TasksContainer = styled.div`
  flex: 1;
  overflow: scroll;
  ::-webkit-scrollbar {
    display: none;
  }
`


