import { QueryKey, useQueryClient } from "react-query";
import { nanoid } from 'nanoid'
import { reorder } from '../utils/reorder'
import { Task } from '../types/task'

// 生产乐观更新配置
export function useConfig(queryKey: QueryKey , callback: (target: any, old?: any[]) => any[]) {
  const queryClient = useQueryClient()
  return {
    onSuccess: () => queryClient.invalidateQueries(queryKey), // 重新获取列表数据
    async onMutate(target: any) {
      // 根据queryKey获取全局中对应的数据
      const previousItems = queryClient.getQueryData(queryKey)
      // 对原始数据进行操作
      queryClient.setQueryData(queryKey,(old?: any[]) => {
        return callback && callback(target,old)
      })
      return { previousItems } // 把当前数据返回出去,回滚需要用到
    },
    /*
      当请求被调用,立即会触发onMutate,即使请求尚未完成,view层也会立即更新.
      如果请求返回错误,将会走到onError里,需要对数据进行回滚
    */
    onError(error: any,newItem: any,context: any) {
      queryClient.setQueryData(queryKey, context.previousItems)
    }
  }
}

// 新增
export const useAddConfig = (queryKey:QueryKey) => {
  return useConfig(queryKey,(target,old) => old?.length ? [...old, { ...target,id: nanoid() }] : [] )
}

// 删除
export const useDeleteConfig = (queryKey:QueryKey) => {
  return useConfig(queryKey,(target,old) => {
    return old?.filter(item => item.id !== target.id) || []
  })
}
// 收藏
export const useCollectConfig = (queryKey:QueryKey) => {
  return useConfig(queryKey,(target,old) => {
    return old?.map(item => {
      if (item.id === target.id) {
        item.pin = target.pin
      }
      return item
    }) || []
  })
}

// 编辑
export const useEditConfig = (queryKey:QueryKey) => {
  return useConfig(queryKey,(target,old) => {
    return old?.map(item => {
      if (item.id === target.id) {
        item = target
      }
      return item
    }) || []
  })
}

// 看板移动排序
export const useReorderKanbanConfig = (queryKey:QueryKey) => {
  return useConfig(queryKey,(target,old) => reorder({ list: old, ...target }))
}

// task移动排序
export const useReorderTaskConfig = (queryKey:QueryKey) => {
  return useConfig(queryKey,(target,old) => {
    // 乐观更新task序列中的位置
    const orderedList = reorder({ list: old, ...target }) as Task[];
    // 由于task排序可能还涉及到所属kanban到变化，所以不要忘了改变kanbanId
    return orderedList.map((item) =>
      item.id === target.fromId
        ? { ...item, kanbanId: target.toKanbanId }
        : item
    );
  })
}
