import { useEffect, useRef } from "react";
/*
*  返回组件的挂载状态,如果还未挂载或者已经卸载，则返回false; 反之返回true
* */

// 判断当前组件是否已卸载
export default function useMountedRef() {
  const mountedRef = useRef(false)

  useEffect(() => {
    mountedRef.current = true
    return () => {
      mountedRef.current = false
    }
  })
  return mountedRef
}
