import React, { useEffect } from "react";
import {
  Modal, Form, Input, Button
} from "antd";

import { useTasksModal, useTasksQueryKey } from "./util";
import { useDeleteTask, useEditTask } from "../../hooks/useTasks";

import UserSelect from "../../components/user-select";
import TaskTypeSelect from "../../components/task-type-select";
import { ExclamationCircleOutlined } from "@ant-design/icons";

const layout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 20 }
}

export default function TaskModal() {
  const [form] = Form.useForm()

  const {
    editTaskModalVisible, editTaskList, close,
  } = useTasksModal()

  const { mutateAsync: editTask, isLoading } = useEditTask(useTasksQueryKey())
  const { mutateAsync: deleteTask } = useDeleteTask(useTasksQueryKey())

  useEffect(() => {
    if (editTaskList) {
      form.setFieldsValue(editTaskList)
    }
  },[editTaskList, form])

  const onFinish = async (values: any) => {
    await editTask({ ...editTaskList, ...values })
    close()
  }

  const startEdit = () => {
    close()
    Modal.confirm({
      title: '您确定要删除任务吗?',
      icon: <ExclamationCircleOutlined />,
      content: '这是一条删除提示',
      async onOk() {
        if (editTaskList?.id) {
          await deleteTask(editTaskList.id)
        }
      }
    })
  }

  return (
    <Modal
      title={'编辑任务'}
      visible={editTaskModalVisible}
      okText={'确认'}
      cancelText={'取消'}
      confirmLoading={isLoading}
      onOk={form.submit}
      onCancel={() => {
        form.resetFields()
        close()
      }}
    >
      <Form
        {...layout}
        form={form}
        onFinish={onFinish}
      >
        <Form.Item
          label={'任务名'}
          name={'name'}
          rules={[{ required: true,message: '请输入任务名' }]}
        >
          <Input/>
        </Form.Item>

        <Form.Item
          label={'经办人'}
          name={'processorId'}
        >
          <UserSelect defaultOptionName={'经办人'}/>
        </Form.Item>

        <Form.Item
          label={'类型'}
          name={'typeId'}
        >
          <TaskTypeSelect/>
        </Form.Item>
      </Form>
      <div style={{ textAlign: "right"}}>
        <Button
          style={{ fontSize: 14 }}
          size={"small"}
          onClick={startEdit}
        >删除</Button>
      </div>
    </Modal>
  )
}
